::selection {
  background-color: $primary;
  color: $white;
}
body {
  font-family: 'Poppins', sans-serif;
  position: relative;
  font-size: 14px;
  color: $secondary;
  overflow-x: hidden;
  &.event {
    font-family: 'Work Sans', sans-serif;
  }
}
h1, h2 {
  font-family: 'Capriola', sans-serif;
  text-transform: capitalize;
}
h4, h5, li {
  margin-top: 0;
  margin-bottom: 0;
}
h1, h2, h3, h4, h5, h6 {
  text-transform: capitalize;
}
h2, h3, h4, h5, h6, li, a {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  line-height: normal;
}
h2, h3, h4 {
  color: $primary;
}
h5, h6, p {
  color: $secondary;
}
h1 {
  font-size: calc(22px + (50 - 22) * ((100vw - 300px) / (1920 - 300)));
  color: $white;
  font-weight: 400;
  letter-spacing: 2px;
  margin-top: -7px;
  margin-bottom: -20px;
  line-height: 1.4;
  span {
    &.small-text {
      font-size: calc(18px + (40 - 18) * ((100vw - 300px) / (1920 - 300)));
      color: $white;
    }
    &.bold-text {
      font-weight: 600;
    }
    &.theme-color {
      color: $primary;
    }
  }
}
h2 {
  font-size: calc(20px + (36 - 20) * ((100vw - 300px) / (1920 - 300)));
  line-height: 0.9;
}
h3 {
  font-size: calc(18px + (26 - 18) * ((100vw - 300px) / (1920 - 300)));
  margin-top: -4px;
  margin-bottom: 0;
}
h4 {
  font-size: calc(16px + (20 - 16) * ((100vw - 300px) / (1920 - 300)));
}
h5 {
  font-size: calc(14px + (16 - 14) * ((100vw - 300px) / (1920 - 300)));
}
h6 {
  font-size: calc(13px + (14 - 13) * ((100vw - 300px) / (1920 - 300)));
  margin-top: -4px;
  margin-bottom: -5px;
}
p {
  font-size: calc(13px + (14 - 13) * ((100vw - 300px) / (1920 - 300)));
  color: $secondary;
  font-weight: 400;
  margin-top: -7px;
  margin-bottom: -4px;
  line-height: 1.8;
}
body{
  header.fixed {
    position: sticky;
    top: 0;
    left: 0;
    transition: all 0.5s ease;
    z-index: 9;
    nav{
      padding: 0px 0;
    }  
  }
}
.table> :not(:last-child)> :last-child>*{
  border-bottom-color: $border-white;
}
a {
  font-size: calc(13px + (14 - 13) * ((100vw - 300px) / (1920 - 300)));
  color: $secondary;
  text-decoration: none;
  &:hover {
    text-decoration: none;
    cursor: pointer;
  }
  &:focus {
    outline: none;
  }
}
.media{
  display: flex;
    align-items: flex-start;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  li {
    font-size: 14px;
