.borders-before {
  margin-left: 60px;
  margin-bottom: 25px;
  text-transform: uppercase;
  span {
    display: inline-block;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      border-top: 2px solid $border-light;
      width: 46px;
      transform: translate(0, -50%);
      top: 50%;
    }
    &:before {
      right: 100%;
      margin-right: 15px;
    }
    &.color-white {
      &:before {
        border-top: 2px solid $white;
      }
    }
  }
}
.bg-theme {
  background: linear-gradient($light, $dark);
}
.center-abs {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.shadow {
  box-shadow: 0 4px 8px 1px $shadow-light;
}
.link-horizontal {
  ul {
    display: flex;
    li {
      padding-right: 35px;
      &.borders-right {
        border-right: 1px solid $white;
        height: 52px;
        margin-right: 35px;
        box-shadow: 0 0 10px 0 white;
        opacity: 0.7;
      }
      &:last-child {
        padding-right: 0;
      }
      a {
        background-color: transparent;
        &.icon-btn {
          i {
            font-size: 27px;
            height: 58px;
            width: 58px;
            color: $primary;
            display: flex;
            border: 2px solid $white;
            background-color: $white;
            border-radius: 50%;
            margin-bottom: 10px;
            transition: all 500ms;
            background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, $primary 50%, $primary 100%);
            background-repeat: no-repeat;
            background-position: 0 0;
            background-size: 90px 180px;
          }
          &:hover {
            i {
              color: $white;
              background-position: 0 -90px;
            }
          }
        }
      }
    }
  }
}
.btn-default {
  padding: 14px 35px;
  font-size: calc(13px + (16 - 13) * ((100vw - 300px) / (1920 - 300)));
  font-weight: 500;
  border: 2px solid;
  text-transform: capitalize;
  border-radius: 50px;
  color: $white !important;
  transition: 0.5s;
  letter-spacing: 0;
  &:hover, &:focus, &:active {
    transition: 0.5s;
    background-color: $white;
    border: 2px solid $primary;
    color: $primary !important;
  }
  &:focus {
    box-shadow: none;
  }
  &.primary-btn {
    border: 1px solid white;
    background-color: $primary;
    &.transparent {
      background-color: transparent;
    }
    &:hover, &:focus, &:active {
      background-color: $white;
      color: $primary !important;
      border: 1px solid $primary;
    }
  }
  &.btn-white {
    transition: 0.5s;
    background-color: $white;
    border: 2px solid $primary;
    color: $primary !important;
    &:hover, &:focus, &:active {
      transition: 0.5s;
      background-color: $primary;
      color: $white !important;
      border: 2px solid $white;
    }
    &.border-not {
      border: none;
    }
  }
  .btn-custom:hover {
    background-position: 100% 0;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out
  }
  &.btn-gradient {
    border: transparent;
    position: relative;
    background-color: $dark;
    background-image: linear-gradient($light, $dark);
    background-image: -webkit-gradient(linear, left top, right top, from($light), color-stop($dark), color-stop($dark), to($light));
    background-image: linear-gradient(to right, $light, $dark, $dark, $light);
    transition: all .4s ease-in-out;
    background-size: 300% 100%;
    &:hover, &:focus, &:active {
      transition: all .4s ease-in-out;
      color: $white !important;
      background-position: 100% 0;
    }
  }
}
.overflow-hide {
  overflow: hidden;
}
.swiper-container {
  width: 100%;
}
.img-container {
  position: relative;
  display: inline-block;
}
.overlay {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  background-image: linear-gradient($dark, $light);
  opacity: 0.5;
  &.reversae {
    background-image: linear-gradient($light, $dark);
  }
}
.list {
  ul {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      padding-right: 60px;
      // padding-bottom: 10px;
      text-transform: uppercase;
      a {
        &.active {
          color: $primary;
        }
      }
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }
  }
}
.socials-lists {
  ul {
    &.socials-horizontal {
      display: flex;
    }
    &.socials-verticle {
      display: inline-block;
    }
    li {
      padding: 0 4px;
      a {
        display: inline-block;
        i {
          height: 50px;
          width: 50px;
          background-color: $primary;
          border-radius: 50px;
          display: flex;
          font-size: 20px;
          color: $white;
          transition: all 800ms;
          border: 2px solid $white;
          background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, $white 50%, $white 100%);
          background-repeat: no-repeat;
          background-position: 0 0;
          background-size: 90px 180px;
        }
        &:hover {
          i {
            background-position: 0 -90px;
            background-color: $white;
            color: $primary;
            border: 1px solid $primary
          }
        }
      }
    }
  }
}
.text-hash {
  color: $hash-color;
  letter-spacing: 5px;
}
