.brand-sliders {
  .brand-slider {
    .item {
      a {
        transition: 0.5s;
        img {
          filter: brightness(0);
          &.wedding-brand {
            filter: grayscale(100);
          }
        }
        &:hover {
          transition: 0.5s;
          img {
            filter: unset;
          }
        }
      }
    }
  }
}
