.team-section {
  .border-around {
    border: 13px solid $white;
    border-radius: 100%;
    box-shadow: 0 0 30px $shadow-light;
    height: 150px;
    width: 150px;
  }
  .team {
    height: 135px;
    width: 135px;
  }
  .teams {
    .our-team {
      .overlay-team {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        transition: all 1s;
        img {
          transition: all 1s;
          opacity: 0;
          transform: translateY(20px);
        }
      }
      .borders {
        border-right: 2px dashed black;
        height: 110px;
        left: 50%;
        transform: translate(-50%, 0%);
        transition: all 1s;
      }
      .set-relative {
        &.set-border {
          padding-bottom: 110px;
        }
      }
      .employee {
        margin-top: 15px;
        text-align: center;
        h6 {
          line-height: 30px;
          transition: all 1s;
          color: $secondary;
        }
        .e-name {
          color: $secondary;
          text-transform: uppercase;
          margin-bottom: 5px;
        }
      }
      &:hover {
        .overlay-team {
          transition: all 1s;
          bottom: 8px;
          img {
            transition: all 1s;
            opacity: 1;
            transform: none;
          }
        }
        .borders {
          transition: all 1s;
          border-right: 2px dashed $primary;
        }
        h6, .e-name {
          transition: all 1s;
          color: $primary;
        }
      }
    }
    &:nth-child(even) {
      margin-top: 30px;
    }
  }
}
/**************************
team css end
*************************/

