.pricing {
  .pricing-slider{
    &.price-margin {
      .price-container{
        margin: 5px 5px 2px 5px;
      }
      margin-bottom: -2px;
    }
  }
  .btn-default {
    padding: 16px 40px;
    border-radius: 50px;
  }
  .set-border {
    background: $lighten-white2;
    margin: 30px 80px;
  }
  .price-container {

    &.bg-white {
      background: $white;
    }
    &.shadows {
      filter: drop-shadow(0 0 6px $shadow-light2);;
      border-radius: 20px;;
      background: $white;
      border: none;
      overflow: hidden;
    }
    .service-feature {
      padding: 50px 0 50px 0;
      background-image: linear-gradient($light, $dark);
      &.bg-white {
        background: $white;
      }
      .price-feature {
        text-transform: uppercase;
        color: $white;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    .price-features {
      margin-bottom: 35px;
      .price-feature {
        color: $light-gray;
        margin-bottom: 15px;
        &.font-primary {
          color: $primary;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    .feature-icon {
      width: auto !important;
      color: $primary;;
      display: inline-block;
      margin-bottom: 10px;
      margin-top: -12px;
      &:before {
        font-size: 55px;
      }
    }
    .price-feature-container {
      padding: 30px 0 100px 0;
      .feature-text {
        padding: 0 30px;
        h3{
          font-weight: 700;
          text-transform: uppercase;
        }
        hr {
          margin-bottom: 2rem;
          margin-top: 1.6rem;
          border-top: 1px solid #f4f4f4;
        }
        .feature-text-heading {
          &.bold {
            font-weight: 700;
          }
        }
      }
      .price-value {
        margin-bottom: 30px;;
        .price {
          font-size: calc(22px + (55 - 22) * ((100vw - 300px) / (1920 - 300)));;
          margin: -22px 0 -15px;
          font-weight: 600;
          .large {
            color: $primary;
            font-size: 55px;
            font-weight: 400;
            &.font-primary {
              color: $primary;
            }
          }
        }
      }
      .set-abs {
        z-index: -1;
        width: 100%;
        img {
          &.pricing-img {
            width: 100% !important;
          }
        }
      }
    }
    &.hover-overlay {
      &:hover {
        background: url("../images/gym/price.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        .font-primary {
          color: $white;
        }
        .price-feature-container {
          .feature-text {
            .feature-icon {
              color: $white;
            }
            .feature-text-heading {
              color: $white;
            }
            .set-border {
              color: $white;
            }
          }
          .price-features {
            .price-feature {
              color: $white;
            }
          }
          .price-value {
            .price {
              color: $white;
            }
            .large {
              color: $white;
            }
          }
        }
      }
    }
  }
  &.yoga, &.saas2 {
    .owl-theme {
      .owl-dots {
        margin-top: 40px !important;
        line-height: 1;
        .owl-dot {
          span {
            background-image: linear-gradient($light, $dark);
          }
          &.active {
            span {
              transform: scale(1.4);
            }
          }
        }
      }
    }
    .owl-item {
      &.active {
        &.center {
          .price-container {
            filter: drop-shadow(0 0 6px $shadow-light2);
            opacity: 1;
            transform: scale(1.1);
            transition: 0.5s;
          }
        }
      }
    }
    .price-margin {
      .price-container{
        margin: 35px 25px;
      }
    }
    .price-container {
      filter: grayscale(100%) drop-shadow(0 0 6px $shadow-light2);
      opacity: 0.6;
      border-radius: 10px;
      .btn-default {
        padding: 14px 46px;
      }

      .service-feature {
        padding: 0;
        background-image: unset;
        .image-top {
          width: 80%;
          margin-left: auto;
        }
        .feature-text {
          position: relative;
          .feature-text-heading {
            position: absolute;
            top: 30%;
            left: 0;
            right: 0;
            transform: translate(0, -30%);
          }
        }
      }
      .price-feature-container {
        padding: 0 0 40px 0;
        .price-feature, .price {
          color: $gray2;
        }
        .price-value {
          .price {
            font-size: initial;
            .large {
              font-size: 42px;
              font-weight: 600;
              background: -webkit-linear-gradient($light, $dark);
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
            }
          }
        }
      }
    }
  }
  &.saas2 {
    .price-container {
      .price-feature-container {
        margin-top: -20px;
      }
    }
  }
  &.app2 {
    .price-container {
      .price-feature-container {
        padding: 50px 0 50px 0;
      }
      &.shadows {
        border-radius: 15px;
      }
    }
    .pricing-slider {
      margin-top: -5px;
    }
  }
  &.agency, &.event, &.gym, &.resume {
    .price-container {
      .price-feature-container {
        padding: 50px 0 50px 0;
        .price-value {
          .price {
            font-size: initial;
            font-weight: 500;
          }
        }
      }
      .price-features .price-feature {
        color: $gray3;
        margin-bottom: 15px;
      }
    }
    .set-border {
      margin: 20px 70px 55px 70px;
    }
  }
  &.event, &.resume {
    .price-container {
      &.shadows {
        filter: unset;
        border-radius: 15px;
      }
      .price-features {
        .price-feature {
          color: $light-gray;
        }
      }
    }
    .btn-default {
      padding: 13px 33px;
      background-color: $primary;
      &:hover{
        transition: all 0.3s ease;
        background-color: $white;
      }
    }
  }
  &.gym {
    .price-container {
      &.shadows {
        border-radius: 4px;
      }
      .price-features {
        .price-feature {
          color: $light-gray;
          margin-bottom: 20px;
        }
      }
    }
  }
  &.wedding {
    .price-container {
      &.shadows {
        border-radius: 0;
      }
      .price-feature-container {
        padding: 50px 0 50px 0;
        .price-value {
          .price {
            font-size: initial;
            margin: 0;
          }
        }
      }
    }
    .set-border {
      background: $lighten-yellow2;
      margin: 20px 0 40px 0;
    }
  }
  &.yoga{
    .animated-bg i{
      left: 0;
      right: 0;
      margin: 0 auto;
      bottom: 50%;
    }
  }
}
/**************************
pricing css end
*************************/

/**************************
5. screenshot section start
*************************/
.screenshot {
  .container {
    margin-bottom: 10px;
  }
  .screen-container {
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    top: -11%;
    img {
      height: 685px;
      width: 438px;
      overflow: hidden;
      z-index: 3;
      position: relative;
    }
  }
  .swiper-container {
    .swiper-wrapper {
      .swiper-slide {
        img {
          height: 537px;
          width: auto;
        }
      }
    }
  }
  .owl-theme {
    &.screenshot-slider {
      .owl-dots {
        margin-top: 60px;
        .owl-dot {
          span {
            width: 16px;
            height: 5px;
            margin-bottom: 0;
          }
          &.active {
            span {
              width: 39px;
              height: 5px;
              background: $primary;
              box-shadow: 0 0 4px 1px $shadow-light;
            }
          }
          &:hover {
            span {
              background: $primary;
              box-shadow: 0 0 4px 1px $shadow-light;
            }
          }
        }
      }
    }
  }
}
/**************************
screenshot section end
*************************/

/**************************
6. download section start
*************************/
.download {
  .center-mobile {
    margin-top: -52px;
    margin-bottom: -80px;
    min-height: 580px;
  }
  .download-img {
    display: flex;
    .download-icon {
      position: relative;
      z-index: 9;
      box-shadow: 5px 8.66px 30px 0 rgb(194, 194, 194);
    }
    .download-center {
      h5 {
        color: $primary;
        text-transform: capitalize;
        margin-bottom: 0;
      }
    }
