      flex-direction: column;
      justify-content: center;
    }
  }
  .information {
    p {
      font-size: calc(14px + (16 - 14) * ((100vw - 300px) / (1920 - 300)));
      margin-top: 45px;
      margin-bottom: 50px;
      letter-spacing: 0;
    }
  }
}
/**************************
download section end
*************************/

/**************************
7. about section start
*************************/
.app1{
  &.fixed{
    background: linear-gradient($light, $dark);
    z-index: 9;
  }
}
.about {
  position: relative;
  overflow: hidden;
  &.app1 {
    h3 {
      font-size: calc(20px + (45 - 20) * ((100vw - 300px) / (1920 - 300)));
      line-height: 45px;
      margin-bottom: 14px;
    }
    .counters {
      border-right: 1px solid $white;
      border-bottom: 1px solid $white;
      &:nth-child(even) {
        border-right: none;
      }
      &:nth-child(n+3) {
        border-bottom: none;
      }
      img {
        margin-bottom: 36px;
      }
      .abouts {
        padding: 80px 0;
        text-align: center;
      }
    }
    .mobile {
      right: 10%;
      &.mobile-img {
        z-index: 3;
        .mobile-screen {
          height: 800px;
        }
      }
      &.rectangle {
        right: -4%;
        animation: movebounce 15s linear infinite;
        z-index: -1;
        &.r-2 {
          animation: movebounce 20s linear infinite;
          right: -2%;
        }
      }
      &.galaxy {
        top: 2%;
        right: -15%;
        z-index: 5;
      }
    }
  }
  &.event {
    h4 {
      color: $white;
    }
    p {
      color: $white;
    }
    .announcer-img {
      display: inline-block;
      position: relative;
      margin-left: 40px;
      margin-bottom: 40px;
      img {
        border-radius: 15px;
        overflow: hidden;
        &:nth-child(3) {
          position: relative;
          z-index: 3;
        }
        &:nth-child(2) {
          position: absolute;
          top: 0;
          transform: translate(-20px, 20px);
          opacity: 0.3;
        }
        &:nth-child(1) {
          z-index: 1;
          position: absolute;
          top: 0;
          -webkit-transform: translate(-20px, -20px);
          transform: translate(-40px, 40px);
          opacity: 0.2;
        }
      }
    }
  }
}
/**************************
about section end
*************************/

/**************************
8. format section start
*************************/
.format {
  .logo {
    padding-bottom: 28px;
  }
  .set-border {
    border-bottom: 2px solid $primary;
    width: 15px;
    margin-bottom: 30px;
    margin-left: 75px;
    display: inline-block;
    position: relative;
    &:before, &:after {
      content: '';
      border-bottom: 2px solid $primary;
      position: absolute;
    }
    &:before {
      width: 65px;
      right: 100%;
      margin-right: 10px;
    }
    &:after {
      left: 100%;
      width: 5px;
      margin-left: 10px;
    }
  }
  .about-icon {
    margin-bottom: 0;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
    .quality {
      color: $secondary;
      margin-bottom: 10px;
    }
    .users {
      font-weight: 600;
      color: $primary;
      font-size: 12px;
      text-transform: uppercase;
      letter-spacing: 0;
    }
  }
  .format-small-text {
    margin-bottom: 0;
  }
  .format-head-text {
    margin-bottom: 50px;
    .about-font-header {
      font-size: calc(20px + (30 - 20) * ((100vw - 300px) / (1920 - 300)));
    }
  }
  .format-sub-text {
    margin-bottom: 35px;
    .sub-heading {
      margin-bottom: 20px;
    }
    .about-para {
      margin-bottom: 30px;
      letter-spacing: 0;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.icon-collection {
  display: flex;
  .about-icon {
    margin: 0 18px;
    filter: drop-shadow(0 0 10px $white4);
    border-radius: 20px;
    background-color: $white;
    transition: 0.5s;
    a {
      padding: 18px 20px;
    }
    &:hover {
      transition: 0.5s;
      background-color: $primary;
      a {
        h5, h6, h4 {
          color: $white;
          transition: 0.5s;
        }
      }
    }
  }
}
/**************************
format section end
*************************/

/**************************
9. service section start
*************************/
.services {

  .service {
    padding: 60px 25px;;
    border: 1px solid $light-white;
    box-shadow: 0 0 10px 5px rgba(253, 248, 247, 0.69);
    border-radius: 10px;
    transition: all 1s;
    text-align: center;
    &:hover {
      .img-block {
        animation: scaleDisappear 0.3s;
      }
    }
    &.service-overlay {
      &:hover {
        transition: all 1s;
        background: linear-gradient($light, $dark);
        border-radius: 10px;
        box-shadow: 0 5px 20px $shadow-light;
        img {
          filter: brightness(0) invert(1);
        }
        .service-feature {
          .feature-text, p {
            color: $white;
          }
        }
      }
    }
    .img-block {
      transition: all 0.5s;
      margin-bottom: 15px;
    }
    .service-feature {
      .feature-text {
        margin-bottom: 22px;
      }
    }
  }
  .service-border {
    position: relative;
    &:before, &:after {
      content: '';
      position: absolute;
    }
    &:first-child {
      &:before {
        border-bottom: 2px solid $light-inner;
        width: 210px;
        right: 0;
        bottom: 0;
      }
      &:after {
        border-right: 2px solid $light-inner;
        height: 210px;
        right: 0;
        bottom: 0;
      }
    }
    &:nth-child(2) {
      &:before {
        border-bottom: 2px solid $light-inner;
        width: 210px;
        left: 0;
        bottom: 0;
      }
    }
    &:nth-child(3) {
      &:after {
        border-right: 2px solid $light-inner;
        height: 210px;
        right: 0;
        top: 0;
      }
    }
  }
  &.app2 {
    .service-container {
      &:hover {
        .service {
          background-color: #fff6f6;
        }
      }
      &:nth-child(n+5) {
        .service {
          border: 1px solid $light-white;
          margin-bottom: 0;
        }
      }
      .service {
        margin-bottom: 30px;
        border-radius: 15px;
        .feature-text {
          color: $secondary;
          letter-spacing: 0;
        }
        p {
          letter-spacing: 0;
        }
      }
    }
  }
  &.resume {
    .container {
      margin-bottom: -25px;
      .service {
        margin-bottom: 25px;
      }
    }
  }
}
/**************************
service section end
*************************/

/**************************
10. subscribe section start
*************************/
.subscribe {
  .button-primary {
    top: 0;
    right: 2%;
    position: absolute;
    display: flex;
    align-items: center;
    height: 100%;
    button {
      font-size: calc(13px + (18 - 13) * ((100vw - 300px) / (1920 - 300)));
      padding: 20px 55px;
      font-weight: 200;
      border-radius: 34px;
      letter-spacing: 3px;
    }
  }
  .form-group {
    width: 100%;
    font-size: 18px;
    margin-bottom: 0;
    input, textarea {
      background-color: $white;
      padding: 30px 35% 30px 35px;
      border-radius: 50px;
      border: 1px solid $lighten-white2;
      font-size: 18px;
      box-shadow: none;
      margin: 0;
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: $gray4;
        opacity: 0.5;
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: $gray4;
        opacity: 0.5;
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: $gray4;
        opacity: 0.5;
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: $gray4;
        opacity: 0.5;
      }
    }
    .flex {
      position: relative;
    }
  }
  &.app1 {
    background: url("../images/app_landing1/subscribe/subscribe.png");
    .button-primary {
      right: 4%;
    }
    &.bg {
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
  &.subscribe-2 {
    .form-group {
      .icon {
        font-size: 30px;
        color: $primary;
        position: absolute;
        left: 50px;
        top: 50%;
        transform: translate(0, -50%);
      }
      input {
        padding-left: 110px;
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: $primary;
          opacity: 0.5;
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color: $primary;
          opacity: 0.5;
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color: $primary;
          opacity: 0.5;
        }
        &:-moz-placeholder { /* Firefox 18- */
          color: $primary;
          opacity: 0.5;
        }
      }
      .input-container {
        display: flex;
        width: 100%;
        margin-bottom: 15px;
      }
    }
  }
  &.music {
    .subscribe-title {
      padding: 0 40px;
      text-align: center;
      .subscribe-main {
        margin-bottom: 15px;
        text-transform: none;
        line-height: normal;
      }
      .subscribe-sub {
        margin-bottom: 25px;
      }
    }
  }
  &.yoga, &.resume {
    .subscribe-head {
      margin-top: -12px;
      margin-bottom: 20px;
      font-size: calc(18px + (35 - 18) * ((100vw - 300px) / (1920 - 300)));
      font-weight: 400;
    }
    .subscribe-sub-head {
      font-size: calc(13px + (16 - 12) * ((100vw - 300px) / (1920 - 300)));
      max-width: 560px;
      line-height: 1.8;
      margin: 0 auto;
    }
  }
  &.event, &.wedding, &.yoga {
    .form-group {
      input {
        font-size: calc(13px + (16 - 13) * ((100vw - 300px) / (1920 - 300)));
      }
    }
    .button-primary {
      button {
        letter-spacing: 0;
        font-weight: 500;
      }
    }
  }
  &.resume {
    .button-primary {
      button {
        letter-spacing: 0;
        font-weight: 500;
        border-radius: 34px;
      }
    }
  }
  &.yoga {
    .button-primary {
      right: 28px;
    }
  }
  &.wedding {
    .button-primary {
      button {
        margin-right: 10px;
      }
    }
  }
}
/**************************
subscribe section end
*************************/

/**************************
11. navigations section start
*************************/
body {
  &.menu-on {
    &:before {
      content: "";
      position: fixed;
      background-color: rgba(0, 0, 0, 0.2);
      height: 100vh;
      width: 100vw;
      z-index: 1;
    }
  }
}
.dropdown-sec {
  .dropdown, .dropdown-submenu {
    display: inline-block;
  }
  a {
    text-transform: capitalize;
  }
  .menu-head {
    font-weight: 600;
    text-transform: uppercase;
    padding: 10px 25px !important;
    &:hover {
      color: unset;
    }
  }
  .right-border {
    border-right: 1px solid $lighten-white;
  }
  ul {
    min-width: 230px;
