            background-image: url("../images/gym/header.jpg");
            background-position: top;
            h1 {
              text-align: center;
              text-transform: uppercase;
              font-style: italic;
            }
            .header-sub-text {
              padding: 0 85px;
              p {
                padding: 0 95px;
              }
            }
          }
          &.music-bg {
            background-image: url("../images/music/slider.jpg");
            background-attachment: fixed;
          }
          &.resume {
            background-image: url("../images/resume/h7-bg-text.png");
            background-size: contain;
          }
          &.saas1-header {
            background-image: url("../images/saas1/slider-banner.jpg");
            
          }
          &.saas2-bg {
            background-image: url("../images/saas2/slider.png");
          }
          &.yogas {
            background-image: url("../images/yoga-img/Layer-8.png");
          }
          &.agency {
            background-image: url("../images/agency/slider/slider.png");
            background-position: right;
          }
        }
        .rotate {
          position: absolute;
          top: 20%;
          transform: rotate(45deg);
          right: 17%;
          z-index: 1;
          width: auto;
          img {
            width: 450px;
          }
        }
        .squares {
          .img-1 {
            animation: changedir 10s infinite;
          }
          .img-2 {
            animation: changedir-rev 10s infinite;
          }
        }
        .animation-circle-inverse {
          opacity: 0.25;
          position: absolute;
          right: 100px;
          top: 0;
          i {
            background: $lighten-white;
            position: absolute;
            right: 0;
            height: 200px;
            width: 200px;
            border-radius: 100%;
            opacity: 0.3;
            -webkit-animation: ripple1 3s linear infinite;
            animation: ripple1 3s linear infinite;
            &:nth-child(2) {
              -webkit-animation: ripple2 3s linear infinite;
              animation: ripple2 3s linear infinite;
            }
            &:nth-child(3) {
              -webkit-animation: ripple3 3s linear infinite;
              animation: ripple3 3s linear infinite;
            }
          }
        }
        .wave {
          position: absolute;
          bottom: 0;
          width: 100%;
          animation: move-img 10s linear infinite;
          background-image: url("../images/app_landing2/wave.png");
          height: 40px;
          background-size: contain;
          &:before {
            content: "";
            width: 100%;
            position: absolute;
            bottom: 0;
            animation: move-img-reverse 10s linear infinite;
            background-image: url("../images/app_landing2/wave.png");
            height: 40px;
            background-size: contain;
            opacity: 0.4;
          }
          &:after {
            content: "";
            position: absolute;
            width: 100%;
            bottom: 0;
            animation: move-img 20s linear infinite;
            background-image: url("../images/app_landing2/wave.png");
            height: 45px;
            background-size: contain;
            opacity: 0.4;
          }
        }
