            background-image: url('../images/app_landing2/wave-orange.png');
            height: 34px;
            opacity: 0.4;
            background-size: contain;
          }
          &:after {
            content: "";
            position: absolute;
            bottom: 0;
            opacity: 0.4;
            width: 326px;
            -webkit-animation: move-img 40s linear infinite;
