      .wedding-time {
        margin-bottom: 60px;
        .count {
          position: relative;
          padding-left: 15px;
          padding-bottom: 15px;
          .days {
            font-family: 'Alex Brush', cursive;
            display: block;
            .days-text {
              font-family: 'Alex Brush', cursive;
            }
          }
          .set-border-left {
            position: absolute;
            border-left: 5px solid $dark;
            height: calc(100% - 20px);
            left: 0;
            bottom: 0;
          }
          &:before, &:after {
            content: '';
            position: absolute;
          }
          &:before {
            border-top: 5px solid $dark;
            left: 0;
            width: 15px;
            bottom: calc(100% - 20px);
          }
          &:after {
            border-right: 5px solid $light;
            top: unset;
            left: calc(100% - 20px);
