      .loader-wrapper {
        height: 100vh;
        width: 100vw;
        background-color: $white;
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        z-index: 9999;
        top: 0;

        .loader {
          position: relative;
          display: grid;
          grid-template-columns: 33% 33% 33%;
          grid-gap: 2px;
          width: 100px;
          height: 100px;

          > div {
            position: relative;
            display: inline-block;
            width: 100%;
            height: 100%;
            background: $primary;
            transform: scale(0.0);
            transform-origin: center center;
            animation: loader 2s infinite linear;
            &:nth-of-type(1),
            &:nth-of-type(5),
            &:nth-of-type(9) {
              animation-delay: 0.4s;
            }

            &:nth-of-type(4),
            &:nth-of-type(8) {
              animation-delay: 0.2s;
            }

            &:nth-of-type(2),
            &:nth-of-type(6) {
              animation-delay: 0.6s;
            }

            &:nth-of-type(3) {
              animation-delay: 0.8s;
            }
          }
        }

        @keyframes loader {
          0% {
            transform: scale(0.0);
          }
          40% {
            transform: scale(1.0);
          }
          80% {
            transform: scale(1.0);
          }
          100% {
