        .sub-text {
          margin-top: 50px;
          margin-bottom: 10px;
        }

        .objects img {
          z-index: 90;
          pointer-events: none;
        }

        .object_rocket {
          z-index: 95;
          position: absolute;
          transform: translateX(-50px);
          top: 75%;
          pointer-events: none;
          width: 150px;
          animation: rocket-movement 20s linear infinite both running;
        }

        .object_earth {
          position: absolute;
          top: 20%;
          left: 15%;
          z-index: 90;
          width: 100px;
        }

        .object_moon {
          position: absolute;
          top: 12%;
          left: 25%;
          width: 80px;
        }

        .object_astronaut {
          width: 170px;
          animation: rotate-astronaut 200s infinite linear both alternate;
        }

        .box_astronaut {
          z-index: 110 !important;
          position: absolute;
          top: 60%;
          right: 16%;
          will-change: transform;
          animation: move-astronaut 5s infinite linear both alternate;
        }

        .image-404 {
          position: relative;
          z-index: 100;
          pointer-events: none;
        }

        .stars {
          background: url("../images/404/overlay_stars.svg");
          background-repeat: repeat;
          background-size: contain;
          background-position: left top;
          height: 100vh;

        }

        .text-inner {
          font-size: calc(100px + (250 - 100) * ((100vw - 300px) / (1920 - 300)));
          font-weight: 700;
          margin-top: -62px;
          line-height: 1;
        }

        .glowing_stars .star {
          position: absolute;
          border-radius: 100%;
          background-color: $white;
          width: 3px;
          height: 3px;
          opacity: 0.3;
          will-change: opacity;
        }

        .glowing_stars .star:nth-child(1) {
          top: 80%;
          left: 25%;
          animation: glow-star 2s infinite ease-in-out alternate 1s;
        }

        .glowing_stars .star:nth-child(2) {
          top: 20%;
          left: 40%;
          animation: glow-star 2s infinite ease-in-out alternate 3s;
        }

        .glowing_stars .star:nth-child(3) {
          top: 25%;
          left: 25%;
          animation: glow-star 2s infinite ease-in-out alternate 5s;
        }

        .glowing_stars .star:nth-child(4) {
          top: 75%;
          left: 80%;
          animation: glow-star 2s infinite ease-in-out alternate 7s;
        }

        .glowing_stars .star:nth-child(5) {
          top: 90%;
          left: 50%;
          animation: glow-star 2s infinite ease-in-out alternate 9s;
        }
