              content: '';

            }
            opacity: 1;
            position: relative;
          }
          img {
            width: 100%;
            height: auto;
          }
        }
        .isotopeSelector {
          img {
            transition: all .4s ease;
          }
          .overlay-background {
            transform: scale(0);
            transition: all .4s ease;
            i {
              position: absolute;
              top: 50%;
              left: 50%;
              background-color: $white;
              transform: translate(-50%, -50%);
              color: $primary;
              font-size: 22px;
              display: none;
              transition: all 0.4s ease;
              width: 50px;
              height: 50px;
              align-items: center;
              justify-content: center;
            }
          }
          .overlay {
            &:hover {
              img {
                transform: scale(1.2) rotate(2deg);
                transition: all .4s ease;
              }
              .overlay-background {
                transform: scale(1);
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
                background-color: rgba($black, 0.5);
                transition: all 0.4s ease;
