        box-shadow: 0 15px 30px 0 $border-white;
        position: absolute;
        height: 100px;
        width: 100px;
        border-radius: 100%;
        opacity: 0.3;
        transform: scale(1.3);
        animation: ripple1 3s linear infinite;
        &:nth-child(2) {
          animation: ripple2 3s linear infinite;
        }
        &:nth-child(3) {
          animation: ripple3 3s linear infinite;
        }
      }
      @keyframes ripple1 {
        0% {
          transform: scale(5.5);
          opacity: 0.3;
        }
        100% {
          transform: scale(8.5);
          opacity: 0.0;
        }
      }
      @keyframes ripple2 {
        0% {
          transform: scale(3.5);
        }
        100% {
          transform: scale(5.5);
        }
      }
      @keyframes ripple3 {
        0% {
          transform: scale(1.5);
        }
        100% {
          transform: scale(3.5);
        }
      }


