          >ul{
            li{
              &+li{
                margin-top: 15px;
              }
              a{
                color: $gray12;
              }
            }
          }
          .contact-list{
            li{
              color: $gray12;
              padding-left: 25px;
              position: relative;
              i{
                position: absolute;
                left: 0;
                top: 3px;
              }
            }
          }
        }
        .footer-social{
          ul{
            li{
              display: inline-block;
              height: 35px;
              width: 35px;
              background-color: $gray13;
              text-align: center;
              line-height: 2.6;
              margin-top: 20px;
              margin-right: 10px;
              border-radius: 100%;
            }
          }
        }
        .footer-logo,h4{
          margin-bottom: 30px;
        }
        p{
          color: $gray12;
        }
        .footer-mobile-title{
          display: none;
        }
      }
      .bg-decore{
        background-size: cover;
        background-attachment: fixed;
        height: 100%;
        width: 100%;
        padding: 0 15px;
        animation: 10s linear 0s normal none infinite animate;
      }
      .modal1{
        .modal-bg{
          height: 350px;
        }
        p{
          padding-top: 10px;
        }
        .form-group{
          input{
            padding: 12px 20px;
            margin-bottom: 15px;
            margin-top: 20px;
          }
        }
        .offer-content{
          display: flex;
          align-items: center;
          justify-content: center;
          padding-right: 30px;
          text-align: center;
          > div{
            padding: 30px;
          }
        }
        .close{
          position: absolute;
          top: 10px;
          right: 30px;
          background-color: transparent;
          border: none;
          font-size: 24px;
        }
      }
      @keyframes animate {
        from {
          background-position: 0 0;
        }
        to {
          background-position: 0 500px;
        }
      }
      .down-banner{
        padding-top: 0!important;
        >.container{
          position: relative;
        }
        .banner-contain{
          margin-left: 50px;
          .btn-default{
            padding: 8px 21px;
            font-size: 13px;
          }
        }
        .small-banner{
          height: 250px;
        }
        .banner-contain {
          position: absolute;
          height: 100%;
          max-width: 50%;
          top: 0;
          display: flex;
          align-items: center;
        }
      }
      .parallax-banner{
        padding: 0 !important;
        .full-banner{
          height: 500px;
          background-attachment: fixed;
        }
        .banner-contain{
          position: absolute;
          height: 100%;
          max-width: 25%;
          top: 0;
          display: flex;
          align-items: center;
          h2{
            margin-bottom: 30px;
          }
          .btn-default{
            padding: 8px 21px;
            font-size: 13px;
          }
        }
      }
      .ecommerce-tab{
        .isotopeContainer{
          .product-box{
            margin-bottom: 30px;
          }
          margin-bottom: -30px;
        }
        .filter-section {
          .filter-container {
            ul.filter {
              > li{
                display: inline-block;
                &.active{
                  background-color: $gray4;
                }
              }
            }
          }
        }
      }






